						</uploads>
					</el-form-item>
				</el-col>
				<el-col :span="13">
					<el-form-item label="活动分类" prop="huodongfenlei">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.huodongfenlei?true:false"
							v-model="form.huodongfenlei" 
							placeholder="请选择活动分类"
							style="width:100%;"
							>
							<el-option v-for="(item,index) in huodongfenleiLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="13">
					<el-form-item label="活动时间" prop="huodongshijian">
						<el-date-picker
							class="list_date"
							v-model="form.huodongshijian"
							format="YYYY 年 MM 月 DD 日"
							value-format="YYYY-MM-DD"
							type="datetime"
							:readonly="!isAdd||disabledForm.huodongshijian?true:false"
							placeholder="请选择活动时间"
							style="width: 100%;" />
					</el-form-item>
				</el-col>
				<el-col :span="13">
					<el-form-item label="活动地点" prop="huodongdidian">
						<el-input class="list_inp" v-model="form.huodongdidian" placeholder="活动地点"
							 type="text" 							:readonly="!isAdd||disabledForm.huodongdidian?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="主办方账号" prop="zhubanfangzhanghao">
						<el-input class="list_inp" v-model="form.zhubanfangzhanghao" placeholder="主办方账号"
							 type="text" 							:readonly="!isAdd||disabledForm.zhubanfangzhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="主办方名称" prop="zhubanfangmingcheng">
						<el-input class="list_inp" v-model="form.zhubanfangmingcheng" placeholder="主办方名称"
							 type="text" 							:readonly="!isAdd||disabledForm.zhubanfangmingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="报名时间" prop="baomingshijian">
						<el-date-picker
							class="list_date"
							v-model="form.baomingshijian"
							format="YYYY-MM-DD HH:mm:ss"
							value-format="YYYY-MM-DD HH:mm:ss"
							type="datetime"
							style="width:100%;"
							:readonly="!isAdd||disabledForm.baomingshijian?true:false"
							placeholder="请选择报名时间" />
					</el-form-item>
				</el-col>
				<el-col :span="13">
					<el-form-item label="报名人数" prop="baomingminge">
						<el-input class="list_inp" v-model.number="form.baomingminge" placeholder="报名人数"
							 type="text" 							:readonly="!isAdd||disabledForm.baomingminge?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="用户账号" prop="yonghuzhanghao">
						<el-input class="list_inp" v-model="form.yonghuzhanghao" placeholder="用户账号"
							 type="text" 							:readonly="!isAdd||disabledForm.yonghuzhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="用户姓名" prop="yonghuxingming">
						<el-input class="list_inp" v-model="form.yonghuxingming" placeholder="用户姓名"
							 type="text" 							:readonly="!isAdd||disabledForm.yonghuxingming?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="报名状态" prop="baomingzhuangtai">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.baomingzhuangtai?true:false"
							v-model="form.baomingzhuangtai" 
							placeholder="请选择报名状态"
							style="width:100%;"
							>
							<el-option v-for="(item,index) in baomingzhuangtaiLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="13">
					<el-form-item label="年龄" prop="nianling">
						<el-input class="list_inp" v-model="form.nianling" placeholder="年龄"
							 type="text" 							:readonly="!isAdd||disabledForm.nianling?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="重大疾病" prop="zhongdajibing">
						<el-input class="list_inp" v-model="form.zhongdajibing" placeholder="重大疾病"
							 type="text" 							:readonly="!isAdd||disabledForm.zhongdajibing?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="是否残疾" prop="shifoucanji">
						<el-input class="list_inp" v-model="form.shifoucanji" placeholder="是否残疾"
							 type="text" 							:readonly="!isAdd||disabledForm.shifoucanji?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="16">
					<el-form-item label="报名须知" prop="baomingxuzhi">
						<el-input v-model="form.baomingxuzhi" placeholder="报名须知" type="textarea"
						:readonly="!isAdd||disabledForm.baomingxuzhi?true:false"
						/>
					</el-form-item>
				</el-col>
			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick">取消</el-button>
				<el-button class="formModel_confirm" @click="save"
					type="success"
					>
					保存
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//基础信息
	const tableName = 'huodongbaoming'
	const formName = '活动报名'
	//基础信息
	const breadList = ref([{
		name: formName
	}])
	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form表单
	const form = ref({
		fabiaoshijian: '',
		huodongmingcheng: '',
		tupian: '',
		huodongfenlei: '',
		huodongshijian: '',
		huodongdidian: '',
		baomingxuzhi: '',
		zhubanfangzhanghao: '',
		zhubanfangmingcheng: '',
		baomingshijian: '',
		baomingminge: '1',
		yonghuzhanghao: '',
		yonghuxingming: '',
		sfsh: '待审核',
		shhf: '',
		baomingzhuangtai: '已报名',
		nianling: '',
		zhongdajibing: '',
		shifoucanji: '',
		crossuserid: '',
		crossrefid: '',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		fabiaoshijian : false,
		huodongmingcheng : false,
		tupian : false,
		huodongfenlei : false,
		huodongshijian : false,
		huodongdidian : false,
		baomingxuzhi : false,
		zhubanfangzhanghao : false,
		zhubanfangmingcheng : false,
		baomingshijian : false,
		baomingminge : false,
		yonghuzhanghao : false,
		yonghuxingming : false,
		sfsh : false,
		shhf : false,
		baomingzhuangtai : false,
		nianling : false,
		zhongdajibing : false,
		shifoucanji : false,
		crossuserid : false,
		crossrefid : false,
	})
	const isAdd = ref(false)
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		fabiaoshijian: [
		],
		huodongmingcheng: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		tupian: [
		],
		huodongfenlei: [
		],
		huodongshijian: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		huodongdidian: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		baomingxuzhi: [
		],
		zhubanfangzhanghao: [
		],
		zhubanfangmingcheng: [
		],
		baomingshijian: [
		],
		baomingminge: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		yonghuzhanghao: [
		],
		yonghuxingming: [
		],
		sfsh: [
		],
		shhf: [
		],
		baomingzhuangtai: [
		],
		nianling: [
		],
		zhongdajibing: [
		],
		shifoucanji: [
		],
		crossuserid: [
		],
		crossrefid: [
		],
	})
	//图片上传回调
	const tupianUploadSuccess=(e)=>{
		form.value.tupian = e
	}
	//活动分类列表
	const huodongfenleiLists = ref([])
	//报名状态列表
	const baomingzhuangtaiLists = ref([])
	//methods

	//methods
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
			form.value.fabiaoshijian = context?.$toolUtil.getCurDateTime()
			form.value.baomingshijian = context?.$toolUtil.getCurDateTime()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='fabiaoshijian'){
					form.value.fabiaoshijian = row[x];
					disabledForm.value.fabiaoshijian = true;
					continue;
				}
				if(x=='huodongmingcheng'){
					form.value.huodongmingcheng = row[x];
					disabledForm.value.huodongmingcheng = true;
					continue;
				}
				if(x=='tupian'){
					form.value.tupian = row[x];
					disabledForm.value.tupian = true;
					continue;
				}
				if(x=='huodongfenlei'){
					form.value.huodongfenlei = row[x];
					disabledForm.value.huodongfenlei = true;
					continue;
				}
				if(x=='huodongshijian'){
					form.value.huodongshijian = row[x];
					disabledForm.value.huodongshijian = true;
					continue;
				}
				if(x=='huodongdidian'){
					form.value.huodongdidian = row[x];
					disabledForm.value.huodongdidian = true;
					continue;
				}
				if(x=='baomingxuzhi'){
					form.value.baomingxuzhi = row[x];
					disabledForm.value.baomingxuzhi = true;
					continue;
				}
				if(x=='zhubanfangzhanghao'){
					form.value.zhubanfangzhanghao = row[x];
					disabledForm.value.zhubanfangzhanghao = true;
					continue;
				}
				if(x=='zhubanfangmingcheng'){
					form.value.zhubanfangmingcheng = row[x];
					disabledForm.value.zhubanfangmingcheng = true;
					continue;
				}
				if(x=='baomingshijian'){
					form.value.baomingshijian = row[x];
					disabledForm.value.baomingshijian = true;
					continue;
				}
				if(x=='baomingminge'){
					form.value.baomingminge = row[x];
					disabledForm.value.baomingminge = true;
					continue;
				}
				if(x=='yonghuzhanghao'){
					form.value.yonghuzhanghao = row[x];
					disabledForm.value.yonghuzhanghao = true;
					continue;
				}
				if(x=='yonghuxingming'){
					form.value.yonghuxingming = row[x];
					disabledForm.value.yonghuxingming = true;
					continue;
				}
				if(x=='baomingzhuangtai'){
					form.value.baomingzhuangtai = row[x];
					disabledForm.value.baomingzhuangtai = true;
					continue;
				}
				if(x=='nianling'){
					form.value.nianling = row[x];
					disabledForm.value.nianling = true;
					continue;
				}
				if(x=='zhongdajibing'){
					form.value.zhongdajibing = row[x];
					disabledForm.value.zhongdajibing = true;
					continue;
				}
				if(x=='shifoucanji'){
					form.value.shifoucanji = row[x];
					disabledForm.value.shifoucanji = true;
					continue;
				}
				if(x=='crossuserid'){
					form.value.crossuserid = row[x];
					disabledForm.value.crossuserid = true;
					continue;
				}
				if(x=='crossrefid'){
					form.value.crossrefid = row[x];
					disabledForm.value.crossrefid = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.baomingminge = 0
			disabledForm.value.baomingminge = false;
			form.value.baomingminge='1'
			form.value.sfsh='待审核'
			form.value.baomingzhuangtai='已报名'
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('zhubanfangzhanghao') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.zhubanfangzhanghao = json.zhubanfangzhanghao
				disabledForm.value.zhubanfangzhanghao = true;
			}
			if(json.hasOwnProperty('zhubanfangmingcheng') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.zhubanfangmingcheng = json.zhubanfangmingcheng
				disabledForm.value.zhubanfangmingcheng = true;
			}
			if(json.hasOwnProperty('yonghuzhanghao') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.yonghuzhanghao = json.yonghuzhanghao
				disabledForm.value.yonghuzhanghao = true;
			}
			if(json.hasOwnProperty('yonghuxingming') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.yonghuxingming = json.yonghuxingming
				disabledForm.value.yonghuxingming = true;
			}
			if(json.hasOwnProperty('nianling') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.nianling = json.nianling
				disabledForm.value.nianling = true;
			}
			if(json.hasOwnProperty('zhongdajibing') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.zhongdajibing = json.zhongdajibing
				disabledForm.value.zhongdajibing = true;
			}
			if(json.hasOwnProperty('shifoucanji') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.shifoucanji = json.shifoucanji
				disabledForm.value.shifoucanji = true;
			}
		})
		context?.$http({
			url: `option/huodongfenlei/huodongfenlei`,
			method: 'get'
		}).then(res=>{
			huodongfenleiLists.value = res.data.data
		})
		baomingzhuangtaiLists.value = "已取消,已报名".split(',')
	}
	//初始化
	//取消
	const backClick = () => {
		history.back()
	}
	//提交
	const save=()=>{
		if(form.value.tupian!=null) {
			form.value.tupian = form.value.tupian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		if(objcross!='') {
			objcross.baomingminge = objcross.baomingminge - form.value.baomingminge
			if(objcross.baomingminge<0){
				context?.$toolUtil.message('报名人数不足','error')
				return false
			}
		}
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									//修改跨表数据
									changeCrossData(objcross)
									history.back()
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							//修改跨表数据
							changeCrossData(objcross)
							history.back()
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
	
</script>
