import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import huodongfenleiList from '@/views/pages/huodongfenlei/list'
import huodongfenleiDetail from '@/views/pages/huodongfenlei/formModel'
import huodongfenleiAdd from '@/views/pages/huodongfenlei/formAdd'
import huodongxinxiList from '@/views/pages/huodongxinxi/list'
import huodongxinxiDetail from '@/views/pages/huodongxinxi/formModel'
import huodongxinxiAdd from '@/views/pages/huodongxinxi/formAdd'
import storeupList from '@/views/pages/storeup/list'
import huodongbaomingList from '@/views/pages/huodongbaoming/list'
import huodongbaomingDetail from '@/views/pages/huodongbaoming/formModel'
import huodongbaomingAdd from '@/views/pages/huodongbaoming/formAdd'
import quxiaobaomingList from '@/views/pages/quxiaobaoming/list'
import quxiaobaomingDetail from '@/views/pages/quxiaobaoming/formModel'
import quxiaobaomingAdd from '@/views/pages/quxiaobaoming/formAdd'
import zhubanfangList from '@/views/pages/zhubanfang/list'
import zhubanfangDetail from '@/views/pages/zhubanfang/formModel'
import zhubanfangAdd from '@/views/pages/zhubanfang/formAdd'
import biaoqianList from '@/views/pages/biaoqian/list'
import biaoqianDetail from '@/views/pages/biaoqian/formModel'
import biaoqianAdd from '@/views/pages/biaoqian/formAdd'
import bushiherenqunList from '@/views/pages/bushiherenqun/list'
import bushiherenqunDetail from '@/views/pages/bushiherenqun/formModel'
import bushiherenqunAdd from '@/views/pages/bushiherenqun/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'huodongfenleiList',
			component: huodongfenleiList
		}, {
			path: 'huodongfenleiDetail',
			component: huodongfenleiDetail
		}, {
			path: 'huodongfenleiAdd',
			component: huodongfenleiAdd
		}
		, {
			path: 'huodongxinxiList',
			component: huodongxinxiList
		}, {
			path: 'huodongxinxiDetail',
			component: huodongxinxiDetail
		}, {
			path: 'huodongxinxiAdd',
			component: huodongxinxiAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'huodongbaomingList',
			component: huodongbaomingList
		}, {
			path: 'huodongbaomingDetail',
			component: huodongbaomingDetail
		}, {
			path: 'huodongbaomingAdd',
			component: huodongbaomingAdd
		}
		, {
			path: 'quxiaobaomingList',
			component: quxiaobaomingList
		}, {
			path: 'quxiaobaomingDetail',
			component: quxiaobaomingDetail
		}, {
			path: 'quxiaobaomingAdd',
			component: quxiaobaomingAdd
		}
		, {
			path: 'zhubanfangList',
			component: zhubanfangList
		}, {
			path: 'zhubanfangDetail',
			component: zhubanfangDetail
		}, {
			path: 'zhubanfangAdd',
			component: zhubanfangAdd
		}
		, {
			path: 'biaoqianList',
			component: biaoqianList
		}, {
			path: 'biaoqianDetail',
			component: biaoqianDetail
		}, {
			path: 'biaoqianAdd',
			component: biaoqianAdd
		}
		, {
			path: 'bushiherenqunList',
			component: bushiherenqunList
		}, {
			path: 'bushiherenqunDetail',
			component: bushiherenqunDetail
		}, {
			path: 'bushiherenqunAdd',
			component: bushiherenqunAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
